import React from 'react';
import Section from './Section';


const styles = {
  container: {
    maxWidth: 500,
    margin: 'auto',
    textAlign: 'left',
  },
};


const Events = props => (
  <Section>
    <h2>Prximo evento</h2>
    <div style={styles.container}>
      <h3>
        <a href="#">
          9 Nov 2017 - Laboratoria Developers Meetup Nov 2017 @ Laboratoria Lima
        </a>
      </h3>
      <ul>
        <li>4pm - Bienvenida</li>
        <li>
          4:15pm - Desde la currcula en GitHub al LMS pasando por el parser y
          algn dolor de cabeza (<a href="https://github.com/lupomontero">@lupomontero</a> / <a href="https://github.com/ivandevp">@ivandevp</a>)
        </li>
        <li>
          5pm - Arquitectura de productos en Laboratoria <a href="https://github.com/giancorzo">@giancorzo</a>
        </li>
        <li>
          5:30pm - tbd
        </li>
        <li>6pm - ... / BeerJS (en algn lugar cerca)</li>
      </ul>
    </div>
  </Section>
);


export default Events;



